body {
  background: $bg-color;
  font-family: $open-sans;
}

.header {
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 767px) {
    padding-top: 22px;
    padding-bottom: 22px;
  }
  @media screen and (min-width: 768px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 42px;
    padding-bottom: 180px;
  }
  &-container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (max-width: 767px) {
      max-width: 480px;
    }
    @media screen and (min-width: 768px) {
      width: 768px;
    }
    @media screen and (min-width: 1280px) {
      width: 1280px;
    }
  }
  &-inner__container {
    @media screen and (min-width: 768px) {
      margin-bottom: 60px;
    }
    @media screen and (min-width: 1280px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 160px;
    }
  }
}
.header-logo {
  width: 80%;
  display: block;
  &__container {
    @media screen and (max-width: 767px) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 0;
    }
  }
  &__menu {
    @media screen and (max-width: 767px) {
      cursor: pointer;
      transition: $animation-timing;
      opacity: 0.4;
      width: 50px;
      height: 50px;
      &:hover,
      &:focus {
        opacity: 1;
      }
    }
    & use {
      pointer-events: none;
    }
    @media screen and (min-width: 768px) {
      display: none;
    }
  }
}
.header-nav {
  z-index: 500;
  @media screen and (max-width: 767px) {
    padding: 6px 11px;
    background-color: #ffffff;
    border-radius: 5px;
    text-align: center;
    position: absolute;
    top: 90px;
    right: 10%;
    transition: $animation-timing;
    transform: translatey(-200%);
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 60px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 0;
  }

  &__list {
    @extend %list-throw;
    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
    }
  }
  &__item {
    @media screen and (min-width: 1280px) {
      &:not(:last-child) {
        margin-right: 27px;
      }
    }
  }
  &__link {
    @extend %link-style;
    text-transform: uppercase;
    font-size: 24px;
    font-family: $roboto;
    @media screen and (min-width: 768px) {
      color: $text-color;
    }
  }
  &__button {
    position: absolute;
    top: -20px;
    right: -20px;
    border: none;
    outline: none;
    background-color: transparent;
    transition: $animation-timing;
    &:hover,
    &:focus {
      transform: scale(1.1);
    }
    @media screen and (min-width: 768px) {
      display: none;
    }
  }
}
.is-open {
  transform: translatey(0);
}

.header-info {
  position: relative;
  &__list {
    @extend %list-throw;
    @media screen and (max-width: 767px) {
      margin-bottom: 22px;
    }
    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row-reverse;
      margin-bottom: 60px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 50px;
    }
  }
  &__logo {
    width: 100%;
    @media screen and (max-width: 767px) {
      margin-bottom: 22px;
    }
  }
  &__item {
    @media screen and (min-width: 768px) {
      width: 50%;
    }
    @media screen and (min-width: 1280px) {
      width: 100%;
    }
  }
  &__item p {
    @extend %text-throw;
    color: $text-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.35;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    @media screen and (min-width: 768px) {
      font-size: 18px;
    }
    @media screen and (min-width: 1280px) {
      &:not(:last-child) {
        margin-bottom: 31px;
      }
    }
  }
  &__button {
    @extend %button-style;
    text-transform: uppercase;
    font-weight: 300;
    @media screen and (max-width: 767px) {
      margin-bottom: 5px;
    }
    @media screen and (min-width: 768px) {
      padding: 22px 95px;
      font-size: 24px;
      margin-bottom: 15px;
    }
  }
  &__button__info {
    @extend %text-throw;
    font-weight: 400;
    line-height: 1.35;
    color: $text-color;
    @media screen and (max-width: 767px) {
      font-size: 10px;
      margin-bottom: 22px;
    }
    @media screen and (min-width: 768px) {
      font-size: 18px;
      margin-bottom: 50px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 0;
    }
  }
  &__button__slogan {
    @extend %text-throw;
    font-weight: 400;
    line-height: 1.35;
    color: $text-color;
    opacity: 0.5;
    text-transform: uppercase;
    @media screen and (max-width: 767px) {
      font-size: 12px;
    }
    @media screen and (min-width: 768px) {
      font-size: 20px;
    }
    @media screen and (min-width: 1280px) {
      display: none;
    }
  }
}
.button-container {
  text-align: center;
  @media screen and (min-width: 1280px) {
    position: absolute;
    bottom: 20%;
    left: 0;
  }
}
.aligned {
  @media screen and (min-width: 1280px) {
    padding-top: 50px;
    align-self: flex-start;
  }
}
.header-info__slogan {
  @media screen and (max-width: 1279px) {
    display: none;
  }
  @extend %text-throw;
  font-weight: 300;
  font-size: 28px;
  line-height: 1.17;
  color: $text-color;
  opacity: 0.5;
  text-transform: uppercase;
}
